.card {
  width: $card-width;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  box-sizing: border-box;
  margin-right: $horizontal-gap;
  margin-bottom: $vertical-gap;
}

.card__image {
  width: 100%;
  margin-bottom: 40px;
}

.card__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
}

.card__item-code {
  font-size: 10px;
  line-height: 14px;
  color: $grey-color;
  margin-top: 4px;
  margin-bottom: 16px;
}

.card__reviews,
.card__price {
  display: flex;
  justify-content: space-between;
}

.card__price {
  margin-top: 24px;
  margin-bottom: 16px;
}

.card__button-buy {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  background: $blue-color;
  border-radius: 5px;
  font-weight: bold;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: white;
  text-decoration: none;
}

.card__button-buy:hover {
  background: #fff;
  border: 1px solid $blue-color;
  color: $blue-color;
}

.card__price-amount {
  font-weight: 700;
  font-size: 14px;
}

.card__price-title {
  font-size: 12px;
  font-weight: 400;
  color: $grey-color;
}

.card__comments-counter {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}
